<template>
    <div class="signin">
        <div id="header">
            <img id="logo" :src="require('../assets/logo.svg')" alt="" />
            <div id="title">{{ title }}</div>
        </div>
        <div id="form">
            <Input v-if="register" class="input" type="text" :icon="require('../assets/user.svg')" label="Usuario" v-model="username"/>
            <Input class="input" type="email" :icon="require('../assets/email.svg')" label="Correo electrónico" v-model="email"/>
            <Input class="input" type="password" :icon="require('../assets/password.svg')" label="Contraseña" v-model="password"/>
        </div>
        <div>
            <div v-if="register" class="button" @click="signup">Registrar</div>
            <div v-else class="button" @click="signin">Acceder</div>

            <div v-if="register" class="note">¿Ya tienes cuenta? <b @click="() => {register = !register; this.title='Acceso'}">Ingresa ...</b></div>
            <div v-else class="note">¿No tienes cuenta? <b @click="() => {register = !register; this.title='Registro'}">Crea una ...</b></div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import Input from '@/components/Input'

export default {
    name: 'Signin',
    components: {
        Input
    },
    data: function() {
        return {
            title: 'Acceso',
            username: '',
            email: '',
            password: '',
            password2: '',
            register: false,
            valid: false
        }
    },
    methods: {
        signin() {
            
            firebase.auth().signInWithEmailAndPassword(this.email, this.password).then(
                (user) => {
                    console.log(user);
                    this.$router.replace({name: 'main_menu'})
                },
                (error) => {
                    console.log(error);
                    this.valid = false;
                }
            )
        },
        signup() {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password).then(
                (user) => {
                    console.log(user);
                    this.$router.replace({name: 'main_menu'})
                },
                (error) => {
                    console.log(error);
                    this.valid = false;
                }
            )
        }
    }
}
</script>

<style scoped>
.signin {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-rows: 42px 55% 30%;
}

#header {
    width: 100;
    height: 100%;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    position: relative;
}

#form {
    margin-left: 5%;
    margin-right: 5%;
    align-self: center;
}

#title {
    position: absolute;
    top: 8px;
    width: 100%;
    text-align: center;
    font-size: 25px;
    color: white;
    font-weight: bold;
}

img {
    width: 32px;
    height: 32px;
    margin: 5px;
}

.button {
    text-align: center;
    background: linear-gradient(135deg, #67fed7 0%,#0fabf9 100%);
    margin-top: 3%; 
    margin-left: 25%;
    margin-right: 25%;
    border-radius: 20px;
    font-size: 20px;
    color: white;
    box-shadow: 0 3px 6px rgba(57,211,232,0.16), 0 3px 6px rgba(57,211,232,0.23);
    height: 40px;
    line-height: 40px;
}

.note {
    text-align: center;
    color: #B5ABAB;
    margin: 5%;
}

@media (min-height: 600px) {
    .signin {
        grid-template-rows: 55% 30% 20%;
    }
    img {
        position: absolute;
        top: 50%;
        left: 50%;
        width: auto;
        height: auto;
        transform: translate(-50%, -50%);
    }
    #title {
        position: absolute;
        top: 90%;
        width: 100%;
        text-align: center;
        font-size: 25px;
        color: white;
        font-weight: bold;
    }
}
</style>
